import React, {Component} from 'react';
import styles from '../sass/components/carousel.scss';
import Card from "./Card";
import Countries from './../data/Countries';
import provider from './../hocs/Provider'
import {connect} from "react-redux";
const env = require('./../../env.env.json');

class Carousel extends Component {

  constructor(props) {
    super(props);

    this.state = {
      country: null,
      universities: null,
      autocomplete: null,
      popUp: false,
      imageError: false
    };

    this.searchTerm = this.searchTerm.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.escFunction = this.escFunction.bind(this);
    this.renderAutocomplete = this.renderAutocomplete.bind(this);
    this.renderContent = this.renderContent.bind(this);
    this.renderCountriesCards = this.renderCountriesCards.bind(this);
  }

  componentDidMount(){
    document.addEventListener("keydown", this.escFunction, false);
  }

  componentWillUnmount(){
    document.removeEventListener("keydown", this.escFunction, false);
  }

  escFunction(event){

  }

  handleChange(event) {
    console.log(this.state.country);
    const term = event.target.value;
    if (this.props.institution.institutions) {
      this.setState({
        autocomplete: this.props.institution.institutions
                        .filter(institution =>
                          institution.attributes.name
                            .toLowerCase().includes(term.toLowerCase()) &&
                            (
                              this.state.country.acronym.toLowerCase() ===
                              institution.attributes.country ||
                              (
                                this.state.country.acronym === 'CO' &&
                                !institution.attributes.country
                              )
                            )
                          )
      });
    }
  }

  searchTerm(term) {

  }

  static renderBackground() {
    return (
      <div
        className={styles.rotateGradient}
        style={{
          background:
            `linear-gradient(127deg, rgba(246, 65, 108, 0.5), rgba(246, 65, 108, 0) 70.71%),linear-gradient(217deg, rgba(31, 207, 203, 0.7), rgba(31, 207, 203, 0) 70.71%),linear-gradient(336deg, rgba(42, 189, 239, 1), rgba(42, 189, 239, 0) 70.71%)`
        }}
      />
    );
  }

  renderAutocomplete() {
    if (this.state.autocomplete) {
      return this.state.autocomplete
        .map(university =>
          <a
            className={styles.university}
            href={
              university.attributes.domain ||
              `https://${university.attributes.slug}.booklick.co`
            }
            onClick={() =>
              university.attributes.domain ||
              `${university.attributes.slug}.booklick.co` ||
              this.props.popUp()
            }
          >
            {!university.attributes.slug || <div className={styles.logoContainer}>
              <img
                src={`${env.bucket}img/universities/${university.attributes.slug}.png`}
                alt={'Logo Booklick'}
              />
            </div>}
            <div className={styles.infoContainer}>
              <h3>{university.attributes.name}</h3>
              <h5>{university.attributes.slug}</h5>
            </div>
          </a>
        );
    }
  }

  renderCountriesCards() {
    return Countries.map((country) =>
      <Card
        data={country}
        key={country.acronym}
        onClick={() => this.setState({country})}
      />
    );
  }

  renderContent() {
    if (this.state.country) {
      return (
        <div className={styles.contentContainer}>
          <div className={styles.logoContainer}>
            <img
              src={this.state.country.illustration}
              alt={'Logo Booklick'}
            />
          </div>
          <h1>LEE, COMPARTE Y APRENDE.</h1>
          <h2>Miles de libros, revistas, y contenido para todos.</h2>
          <div className={styles.search}>
            <div className={styles.inputSearch}>
              <input
                id="SearchInput"
                type="text"
                name="search-term"
                autoComplete="off"
                onChange={this.handleChange}
                placeholder="Busca tu universidad"
                ref={(input) => { this.searchInput = input; }}
              />
              <span
                className={styles.searchButton}
                onClick={() =>
                  this.setState({
                    autocomplete: this.props.institution.institutions
                  })
                }
              >
                <i className={`${styles.downArrow} fas fa-sort-down`} />
              </span>
            </div>
            {!this.state.autocomplete || <div className={styles.autocomplete}>
              { this.renderAutocomplete()}
            </div>}
          </div>
        </div>
      );
    }
    return (
      <div className={styles.contentContainer}>
        <h1 className={styles.titleCountry}>Elige tu país</h1>
        <div className={styles.scroll}>
          <div className={styles.countriesContainer}>
            {this.renderCountriesCards()}
          </div>
        </div>
      </div>
    );
  }

  render() {
    return (
      <div className={styles.carouselContainer}>
        {this.renderContent()}
        <div className={styles.rotateBackground}>
          {Carousel.renderBackground()}
        </div>
      </div>
    );
  }
}

function mapStateToProps(reducers) {
  return {institution: reducers.institution};
}

export default provider(connect(mapStateToProps, {})(Carousel));
