import {ofType} from "redux-observable";
import {
  GET_INSTITUTIONS,
  GET_INSTITUTIONS_OK,
  getInstitutionsOk
} from "../../actions";
import {flatMap, map} from 'rxjs/operators';
import { from } from 'rxjs';
import Institutions from "./../../middlewares/Institutions";
import {institutionInterface} from './index';
const env = require('./../../../env.env.json');

export const getInstitutionsEpic: any = (action$: any) =>
    action$.pipe(
        ofType(GET_INSTITUTIONS),
        flatMap(() => getInstitutionsLogic$()));

// Epics Logic
const getInstitutionsLogic$ = () =>
    from(Institutions.fetchInstitutions()).pipe(
        map((data: any) => getInstitutionsOk(data.data)));

const INITIAL_STATE: institutionInterface = {
    loading: false,
    institutions: []
};

export default function(state = INITIAL_STATE, action: any) {
    switch (action.type) {
        case GET_INSTITUTIONS:
            Institutions.configure(env.msProfile);
            return {
                ...state,
                loading: true,
            };
        case GET_INSTITUTIONS_OK:
            return {
                ...state,
                loading: false,
                institutions: action.payload
            };
        default:
            return state;
    }
}
