import React, {Component} from 'react';
import styles from './../sass/components/card.scss';

class Card extends Component {
  render() {
    return (
      <div className={styles.card} onClick={() => this.props.onClick()}>
        <div className={styles.imgContainer}>
          <img src={this.props.data.illustration} alt={this.props.data.acronym} />
        </div>
        <h2>{this.props.data.title}</h2>
        <h4>{this.props.data.acronym}</h4>
      </div>
    );
  }
}

export default Card;
